* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  background:black;
  /* background: #000 url('img/marvelbg.jpg') no-repeat center center/cover; */
  height: 100vh;
  color:azure;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
}

.btn {
  display: inline-block;
  color: #fff;
  background-color: #3fb573;
  font-size: 1em;
  text-align: center;
  padding: 10px 15px;
  border: 0;
  margin: 10px 0;
}

header {
  height: 150px;
}

header img {
  width: 200px;
}

.firstpara{
  text-align: center;
  color:azure;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.center {
  font-size:40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search {
  height: 100px;
}

input[type='text'] {
  display: block;
  padding: 10px;
  font-size: 20px;
  border: 0;
  border-radius: 5px;
  width: 60%;
  margin: auto;
}

.contents {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem 2rem;
  margin: 0px 10px 0px 10px;

}

.contents.contents-url{
  cursor: pointer;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0rem 1rem;
  background-color:white;
}

.single-url{
  padding: 4px 5px 4px 5px;
  text-transform: uppercase; 
  font-size:12px;
  font-weight: 700;

  background: 
      linear-gradient(rgb(0, 0, 0) 0 0) 
      0 100% /var(--d, 0) 1.5px 
    no-repeat;
  transition:0.5s;
  color:black;

}
.mylinks{
  color:rgb(43, 43, 43);
  padding: 4px 4px 4px 4px;
}

.single-url:hover{
  /* font-size: 14px; */
  --d: 40%;
}


.full-card{
  background-color:white;
}

.content {
  /* cursor: pointer; */
  background-color: black;
  height: 300px;
  perspective: 1000px;
   
  
}

.content h1 {
  font-size: 25px;
  border-bottom: 1px #fff solid;
  padding-bottom: 10px;
  margin-bottom: 10px;
  
}

.content img {
  width: 100%; 
  height: 296px;
  object-fit: cover;
  
} 

.content-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.content:hover .content-inner {
  transform: rotateY(180deg);
  
}

.content-front,
.content-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  overflow-y: auto;


}

.content-back {
  background-color: #333;
  color: white;
  padding: 20px;
  transform: rotateY(180deg);
}

.content li {
  list-style: none;
  padding-bottom: 10px;
}

